@themeColor:#047adf;

/*-----------------------------
        查询表单样式
 ------------------------------*/
.query-form{
  padding: 0 20px;
  height:100%;
  display: flex;
  flex-direction: column;
  >header{
    border-bottom: 1px solid rgb(231,231,231);
    display: none;
    h4{
      padding: 20px 0 20px 20px;
      margin: 0;
    }
  }
  .query-content{
    padding: 0 8px;
    display: flex;
    flex-direction: column;
    height: 200px;
    flex:1;
    .query-tags{
      padding: 5px 0;
      border-bottom: 1px solid #e7e7e7;
      .tag-item{
        display: flex;
        h4{
          padding: 0 10px;
          width: 100px;
          text-align: right;
        }
        .tag-container{
          flex: 1;
          width: 100px;
        }
      }
    }
    .query-condition{
      display: flex;
      padding: 20px 0;
      border-bottom: 1px solid #e7e7e7;
      .query-input{
        display: flex;
        flex-wrap: wrap;
        width: 660px;
        .query-item{
          width: 330px;
          display: flex;
          align-items: center;
          margin-bottom: 10px;
          >label{
            width: 100px;
            margin: 0;
            text-align: right;
            padding: 0 10px;
            white-space: nowrap;
          }
          >div{
            width: 230px;
            >input,
            >select,
            >textarea
            {
              width:100%;
              border-radius: 0;
            }
          }
          >.two-condition{
            display: flex;
            align-items: center;
            >label{
              padding: 0 5px;
            }
            >div{
              >input,
              >select,
              >textarea
              {
                width:100%;
                border-radius: 0;
              }
            }
          }
          .ui-select-bootstrap .ui-select-match-text {
            text-overflow: ellipsis;
            white-space: nowrap;
            overflow: hidden;
          }
        }
      }
      .query-tool{
        flex: 1;
        padding-left: 65px;
      }
      .query-extra{
        width: 110px;
      }
    }
    .tool-bar{
      display: flex;
      height: 50px;
      align-items: center;
      .tool-tag{
        flex:1;
        display: flex;
        width: 100px;
        h4{
          padding: 0 10px;
          width:100px;
        }
        .tag-container{
          flex: 1;
          width: 100px;
        }
      }
      .tool-buttons{
        >button{
          min-width: inherit;
        }
      }
    }
    .content-table{
      flex: 1;
      height: 10px;
      //overflow: auto;
      >table{
        margin: 0;
      }
      >div{
        height: 100%;
        //overflow: auto;
      }
    }
  }
}

.query-form-mini{
  height: 100%;
  position: relative;
  display: flex;
  flex-direction: column;
  &>header{
    height: 45px;
    margin: 0 10px;
    border-bottom: solid 1px #c6cace;
    h4{
      font-size: 16px;
      padding: 16px 0 0 5px;
      margin: 0;
    }
    button{
      position: absolute;
      top: 9px;
      right: 10px;
    }
  }
  .query-content{
    height: 100px;
    flex: 1;
    display: flex;
    flex-direction: column;
    padding: 10px 10px;
    .query-condition{
      margin-bottom: 10px;
      .query-item{
        display: flex;
        label{
          padding: 5px 10px;
          width: 50px;
        }
        &>div{
          flex: 1;
          width:10px;
        }
      }
    }
    .tool-bar{
      text-align: center;
      margin-bottom: 10px;
    }
    .content-table{
      flex: 1;
      height: 10px;
      overflow-y: auto;
      overflow-x: hidden;
    }
  }
}
/*----------------------------
          对话框样式
 -----------------------------*/
.ngdialog.ngdialog-theme-default.custom{
  overflow: hidden;
  height:100%;
  .ngdialog-content{
    background: white;
    border-radius: 0;
    padding: 0;
    width: 1120px;
    height: 100%;
    display: flex;
    flex-direction: column;
    .dialog-header{
      padding: 20px;
      border-bottom: 2px solid @themeColor;
      color: @themeColor;
    }
    .dialog-content{
      height: 300px;
      overflow-y: auto;
      overflow-x: hidden;
      padding: 10px 50px;
      flex:1;
      position: relative;
    }
    .dialog-buttons{
      text-align: center;
      padding: 10px 0;
      border-top: 1px solid #f2f2f2;
      >button{
        margin-right: 40px;
      }
    }
  }
  &.sm .ngdialog-content{
    width: 670px;
  }
}

/*----------------------------
        JQueryDialog对话框
 -----------------------------*/
div.ui-dialog{
  padding:0;
  .ui-dialog-titlebar{
    background: #337fe5;
    .ui-dialog-title{
      color: white;
    }
    .ui-dialog-titlebar-close{
      border: none;
      border-radius: 0;
      //background: #4e8de2;
      //background: url(frame2/img/close.png) no-repeat 0 0 / contain;
    }
  }
}

/*----------------------------
        表格插件样式
 -----------------------------*/
.ui-grid-cell-contents.operate{
  a{
    padding:0 5px;
  }
}
.grid.ui-grid{
  border: none;
}

/*----------------------------
        向导插件样式
 -----------------------------*/
.ui-tour-backdrop {
  background-color: rgba(0, 0, 0, 0.5);
  fill: rgba(0, 0, 0, 0.5);
}


