.form-simple tr.catagory {
  border-bottom: 1px solid #eeeeee;
}
.form-simple tr.catagory td {
  padding: 0;
}
.form-simple tr.catagory span {
  border-bottom: 2px solid #047adf;
  display: inline-block;
  padding: 0 30px 5px 5px;
  color: #047adf;
  margin-top: 42px;
}
.form-simple td {
  border: 0;
  overflow: inherit;
  position: relative;
  vertical-align: bottom;
}
.form-simple td > label {
  margin-bottom: 0;
}
.form-simple td.td-value {
  padding-right: 50px;
}
.form-simple td.td-label label {
