  border-color: #929292;
  height: 30px;
}
.form-simple td > input:focus,
.form-simple td > textarea:focus,
.form-simple td > select:focus,
.form-simple .gm-combotree-div > input:focus,
.form-simple .date-choose-div > input:focus {
  border-width: 0 0 2px 0;
  border-color: #047adf;
