}
.form-simple td > input:not(:read-only):hover,
.form-simple td > textarea:not(:read-only):hover,
.form-simple td > select:not(:read-only):hover,
.form-simple .gm-combotree-div > input:not(:read-only):hover,
.form-simple .date-choose-div > input:not(:read-only):hover {
  border-color: #047adf;
}
.form-simple td .ui-select-container .ui-select-toggle,
.form-simple td .ui-select-container {
