  border: 1px solid #ddd;
  text-align: center;
}
.form-simple .form-table-tool {
  text-align: right;
}
.form-simple .form-table-tool > button {
  min-width: inherit;
}
/*---------------------------------
              表单验证样式
 ---------------------------------*/
.validation-invalid {
  position: absolute;
  color: red;
  font-size: 12px;
  z-index: 999;
}
