html{
  font-family:"Microsoft Yahei","Droid Sans Fallback","Arial","Helvetica","sans-serif","宋体";font-size:12px;outline: none;color: #666;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}
body,html{
  width: 100%;
  height: 100%;
  overflow: hidden;
  background: #f5f5f5;
  ul li{list-style: none;}
}
table{
  border-collapse: collapse;
}
ul{
  list-style-type: none;
}

/*通用*/
.text-nowrap{
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
.un-select{
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.fl{float: left;}
.fr{float: right;}
.l-c-i{vertical-align: middle ! important;}
.bz-bb{box-sizing:border-box;-moz-box-sizing:border-box;-webkit-box-sizing:border-box;}
.bz-cb{box-sizing:content-box;-moz-box-sizing:content-box;-webkit-box-sizing:content-box;}
.bc-w{background: #fff;}
.bc-w-i{background: #fff ! important;}
.fw-in{font-weight: inherit ! important;}
.fw-b{font-weight: bold;}
.cs-p{cursor: pointer ! important;}
.clrfix:after{
  content: "";
  display: block;
  clear: both;
}
.transform180{
  transform:rotate(180deg);
  -ms-transform:rotate(180deg); 	/* IE 9 */
  -moz-transform:rotate(180deg); 	/* Firefox */
  -webkit-transform:rotate(180deg); /* Safari 和 Chrome */
  -o-transform:rotate(180deg); 	/* Opera */
}

@media only screen and (min-width: 320px) {
  html {
    /*此时1rem = 50px;*/
    font-size: 50px;
  }
}
@media only screen and (min-width: 375px) {
  html {
    font-size: 58.59375px;
  }
}
@media only screen and (min-width: 414px) {
  html {
    font-size: 64.6875px;
  }
}
@media only screen and (min-width: 480px) {
  html {
    font-size: 75px;
  }
}
@media only screen and (min-width: 560px) {
  html {
    font-size: 87.5px;
  }
}
@media only screen and (min-width: 640px) {
  html {
    /*此时 1rem = 100px*/
    font-size: 100px;
  }
}
@media only screen and (min-width: 720px) {
  html {
    font-size: 112.5px;
  }
}
@media only screen and (min-width: 750px) {
  html {
    font-size: 117.1875px;
  }
}
@media only screen and (min-width: 800px) {
  html {
    font-size: 125px;
  }
}
@media only screen and (min-width: 960px) {
  html {
    font-size: 150px;
  }
}
@media only screen and (min-width: 960px) and (orientation: landscape) {
  html {
    font-size: 93.75px;
  }
}
@media only screen and (min-width: 1024px) and (orientation: landscape) {
  html {
    font-size: 100px;
  }
}
@media only screen and (min-width: 1280px) and (orientation: landscape) {
  html {
    font-size: 125px;
  }
}

/* bootstab样式修证 */
.tooltip{
  white-space: nowrap !important;
}
.dropdown-menu.datepicker{
  min-width: 300px;
}

/* 选择文件按钮样式 */
.input-file{
  display: none !important;
}

/* angular样式统一修证 */

/* ZUI样式统一修证 */
.nav-tabs{
  li.active a{
    border-bottom: 2px solid #189ACA !important;
  }
}


